input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

html,
body {
  background-color: rgb(195, 214, 255);
  height: 100%;
}

p {
  margin: 5px 0;
}

input {
  text-align: right;
}

.content-wrapper {
  height: 500px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 800px;
}

.title-container {
  background-color: greenyellow;
  display: flex;
  justify-content: center;
  align-items: center;
  height: auto;
  margin: 0 auto;
  width: 200px;
}

.app-title {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-size: 36px;
  font-weight: 400;
}

.main-container {
  background-color: rgb(253, 254, 255);
  border-radius: 15px;
  margin: 64px auto;
  max-width: 100%;
}

.left-container {
  padding-right: 32px;
}

.form-control {
  line-height: 32px;
  padding: 6px 18px;
}

.input-container {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.dollar-icon,
.person-icon {
  height: 15px;
  position: fixed;
  margin: 0 18px;
  width: 10px;
  z-index: 999;
}

.percentage-options-container {
  margin: 0 -5px 25px;
}

.results-container {
  background-color: rgb(184, 112, 166);
  border-radius: 10px;
  padding: 20px 32px;
  position: relative;
}

.reset-wrapper {
  position: absolute;
  bottom: 20px;
  width: calc((100% - (32px * 2)) + 30px);
}

.generated-amt {
  display: flex;
  justify-content: right;
  font-size: 36px;
}

.per-person {
  font-size: 12px;
}

.btn {
  font-size: 18px;
  height: 46px;
}

